@import url("https://fonts.googleapis.com/css?family=Barlow+Condensed");
@import url("../fonts/Lucida Std Bold.otf");
html, body, div, span, applet, object, iframe, h1, h2, h3, h4, h5,
h6, p, blockquote, pre, a, abbr, acronym, address, big,
cite, code, del, dfn, em, img, ins, kbd, q, s, samp, small,
strike, strong, sub, sup, tt, var, b, u, i, center, dl, dt,
dd, ol, ul, li, fieldset, form, label, legend, table, caption,
tbody, tfoot, thead, tr, th, td, article, aside, canvas, details,
embed, figure, figcaption, footer, header, hgroup, menu, nav,
output, ruby, section, summary, time, mark, audio, video {
  margin: 0;
  padding: 0;
  border: 0;
  vertical-align: baseline;
}

html {
  -ms-text-size-adjust: 100%;
  -webkit-text-size-adjust: 100%;
}

img, fieldset, a img {
  border: none;
}

input[type="text"],
input[type="email"],
input[type="tel"],
textarea {
  -webkit-appearance: none;
}

input[type="submit"],
button {
  cursor: pointer;
}

input[type="submit"]::-moz-focus-inner,
button::-moz-focus-inner {
  padding: 0;
  border: 0;
}

textarea {
  overflow: auto;
}

input, button {
  margin: 0;
  padding: 0;
  border: 0;
}

div, input, textarea, select, button,
h1, h2, h3, h4, h5, h6, a, span, a:focus {
  outline: none;
}

ul, ol {
  list-style-type: none;
}

table {
  border-spacing: 0;
  border-collapse: collapse;
  width: 100%;
}

html {
  box-sizing: border-box;
}

*, *:before, *:after {
  box-sizing: inherit;
}

@media (prefers-reduced-motion: reduce) {
  * {
    transition: none !important;
  }
}

body {
  background: #bad1de url(../img/Black-Solid-1.jpg) no-repeat;
  background-size: contain;
  color: #757575;
  font-family: "Barlow Condensed", sans-serif;
}

ul {
  list-style: none;
  display: flex;
  justify-content: center;
}

a {
  text-decoration: none;
  color: inherit;
}

.fullwrap {
  padding: 0 150px;
  padding-top: 33px;
}

.wrapper {
  height: 100%;
  width: 1000px;
  margin: 0 auto;
  border-radius: 5px;
  position: relative;
  background-color: #ededed;
}

.hotest {
  border-radius: 5px;
  height: 42px;
  padding-left: 32px;
  display: flex;
  align-items: center;
}

.hotest__row {
  width: 70%;
  display: flex;
  align-items: center;
}

.hotest span {
  padding: 0 5px 2px;
  background-color: #85afbd;
  text-transform: uppercase;
  color: #fff;
  border-radius: 3px;
  float: left;
}

.hotest p {
  font-family: "Georgia";
  font-style: italic;
  font-size: 11px;
  float: left;
  margin-left: 10px;
}

.hotest p a {
  font-style: normal;
}

.hotest a {
  text-decoration: none;
}

.layman {
  height: 128px;
  display: flex;
  align-items: center;
  height: 128px;
  padding-left: 32px;
  background-size: cover;
  background-image: url("../img/Flag.jpg");
}

.layman__logo {
  text-transform: uppercase;
  color: #fff;
}

.layman__logo--name {
  background-color: #bcbcbc;
  background: linear-gradient(to top, #bcbcbc 0%, white 100%);
  -webkit-background-clip: text;
  -webkit-text-fill-color: transparent;
  font-family: "Lucida Std";
  font-weight: bold;
}

.layman__logo--politics {
  letter-spacing: 14px;
}

.layman__logo--hr {
  margin: 10px 0;
  color: #6892a0;
  width: 150px;
}

.layman__logo--blog {
  color: #6892a0;
  font-size: 11px;
  font-family: 'Lucida Sans Unicode';
  letter-spacing: 2px;
}

.layman__nav {
  margin-top: -35px;
  width: 100%;
  text-transform: uppercase;
  color: #fff;
}

.layman__nav--list .first__ul {
  width: 260px;
  margin-left: 10px;
  margin-bottom: 15px;
  display: flex;
  justify-content: space-between;
  font-size: 13px;
}

.second__ul {
  width: 80%;
  display: flex;
  justify-content: space-between;
}

.second__ul a {
  padding: 4px 10px 5px;
}

.second__ul a:active {
  background: #092f3b;
  box-shadow: 0 0 1px rgba(0, 0, 0, 0.5) inset, 0 1px 2px rgba(0, 0, 0, 0.5) inset, 0 1px 1px rgba(255, 255, 255, 0.1);
}

.main {
  display: flex;
  justify-content: space-between;
  padding-bottom: 30px;
}

.news__line {
  height: 36px;
  display: flex;
  justify-content: flex-start;
  background-color: #cacaca;
  clear: both;
  text-transform: uppercase;
}

.news__line--arrow {
  background-color: #ae0002;
  padding: 0 25px 0 15px;
  color: #fff;
  font-size: 18px;
  display: flex;
  align-items: center;
}

.news__line--triangle {
  width: 0;
  height: 0;
  border-top: 18px solid transparent;
  border-left: 20px solid #ae0002;
  border-bottom: 18px solid transparent;
}

.news__line--sometext {
  padding-left: 15px;
  display: flex;
  align-items: center;
  font-size: 12px;
  color: #757575;
}

.news {
  padding: 30px 16px 0 30px;
}

.news__img {
  margin-bottom: 14px;
}

.news__img--pic {
  height: 256px;
  width: 100%;
  background-image: url("../img/ruins.jpg");
  background-size: cover;
}

.news__img--desc {
  position: absolute;
  height: 110px;
  width: 340px;
  border-radius: 3px;
  margin-top: -150px;
  margin-left: 30px;
  background-color: #1e1e1e;
  font-family: "Georgia";
  color: #fff;
}

.news__img--desc p {
  line-height: 150%;
  text-align: justify;
  padding: 20px;
  font-size: 12px;
}

.news__grid {
  display: flex;
  justify-content: space-between;
  padding-bottom: 44px;
}

.news__blog {
  clear: both;
  height: 130px;
  margin: 20px 0;
  display: flex;
  align-items: center;
}

.news__blog--img {
  float: left;
  margin-right: 25px;
}

.news__blog--desc {
  font-family: "Georgia";
  font-size: 12px;
}

.post {
  display: flex;
  justify-content: space-between;
  margin-top: 20px;
}

.post__comment {
  margin-right: 14px;
}

.post__comment img {
  float: left;
  margin-top: 2px;
}

.post__comment p {
  float: left;
  margin-left: 7px;
  font-size: 11px;
}

.post__tags p {
  float: left;
  margin-right: 10px;
}

.post__tags--link {
  text-decoration: underline;
  font-style: italic;
  color: #2b5e5e;
}

.post__share {
  margin-right: 8%;
}

.post__share p {
  float: left;
  margin-right: 5px;
}

.post__share a {
  float: left;
  margin-left: 5px;
}

.card {
  height: 280px;
  width: 170px;
  border-radius: 3px;
  box-shadow: 1px 1px 4px 0px #adabad;
  font-family: "Georgia";
}

.card__img {
  margin: 14px 0 14px 14px;
}

.card h5 {
  margin: 0 14px 14px;
  color: #042b38;
  text-decoration: underline;
  font-size: 12px;
}

.card__descr {
  margin: 0 14px;
  font-style: italic;
  font-size: 11px;
}

.card__comment {
  float: right;
  margin-right: 14px;
}

.card__comment img {
  float: left;
  margin-top: 2px;
}

.card__comment p {
  float: left;
  margin-left: 7px;
  font-style: italic;
  font-size: 11px;
}

aside {
  margin-right: 40px;
}

.red-line {
  border-radius: 3px;
  width: 188px;
  margin: 30px 0 0 14px;
  height: 36px;
  background-color: #ae0002;
  color: #fff;
  text-transform: uppercase;
  font-size: 18px;
  display: flex;
  align-items: center;
  font-family: "Barlow Condensed", sans-serif;
}

.red-line p {
  margin-left: 15px;
}

.aside {
  border-radius: none;
  box-shadow: none;
  width: 188px;
  min-height: 330px;
}

footer {
  margin: 0 auto;
  width: 1000px;
}

h2 {
  margin: 200px 0 200px;
  font-family: "Lucida Std";
  text-transform: uppercase;
  background: linear-gradient(to top, #0f394a 0%, #266781 100%);
  -webkit-background-clip: text;
  -webkit-text-fill-color: transparent;
}
